import React from "react"
import Layout from "../components/layout"
import SEO from "../components/seo"
import { css, Global } from "@emotion/core";

const AboutPage = () => {
    return (
        <Layout>
            <Global styles={css`
                h1,p{
                    color: white;
                }
            `}
            />
            <SEO title="About" />

            <div css={css`
                display: flex;
                width: 80%;
                margin: 0 auto;
                > div{
                    flex: 2;
                    margin-right: 3rem;


                    p{
                        font-size: 115%;
                        line-height: 2rem;
                    }
                }
            `}>
                <div>
                    <h1>About</h1>
                    <p>
                        テスト
                    </p>
                </div>
            </div>

        </Layout>);
}

export default AboutPage;
